import React from 'react'
import '../sass/styles.css'
import Countdown from 'react-countdown'
import Jackpot from './Jackpot'
import { AlarmOn } from '@material-ui/icons'
import Media from '../media/video.mp4'
import ReactPlayer from 'react-player'

class CuentaReg extends React.Component {

    // componentDidMount() {
    //     let pozomayor = document.getElementById('pozo')
    //     pozomayor.innerHTML = '58.130.543'
    //     let count = 100

    //     let pozodos = document.getElementById('pozo2')
    //     pozodos.innerHTML = '10.000.150'
        

    //     setInterval(function(){
    //         pozomayor.innerHTML = new Intl.NumberFormat().format(58130543 + (count++) * 20)
    //         pozodos.innerHTML = new Intl.NumberFormat().format(10000150 + (count++) * 20)
    //     }, 3000)

    // }

    render() {
        const opts = {
            height: '390',
            width: '640',
            playerVars: {
              // https://developers.google.com/youtube/player_parameters
              autoplay: 1,
            },
          };
    return (
        <section className='count-div' >
            <h3>Próximo sorteo en:</h3>
            <div id='regresiva'>
                <label className='numbers'>
                    <Countdown date={Date.now() + 60000000} />
                </label>
                <ul>
                    <li>dias</li>
                    <li>hs</li>
                    <li>min</li>
                    <li>seg</li>
                </ul>
            </div>
            <div id='cero'>
                <label className='numbers'>
                    <Countdown date={Date.now() - 600000000} />
                </label>
                <ul>
                    <li>dias</li>
                    <li>hs</li>
                    <li>min</li>
                    <li>seg</li>
                </ul>
            </div>
            <Jackpot></Jackpot>
            <button className='btn-sorteo' onClick={sorteo}><AlarmOn></AlarmOn></button>
            <div className="sor-1">
                <div className="bolillero-1">
                    <div className="bolillero">
                        <div className="tubo"></div>
                        <div className="lens" id="lens"></div>
                        <div className="bolillas-content">
                            <div className="balls" id="balls">
                                <label></label>
                                <label></label>
                                <label></label>
                                <label></label>
                                <label></label>
                                <label></label>
                            </div>
                        </div>
                        <div className="base"></div>
                    </div>
                </div>
                <div className='sor-2'>
                    <div className="bolillero-2">
                        <div className="bolillero">
                            <div className="tubo"></div>
                            <div className="lens" id="lens2"></div>
                            <div className="bolillas-content">
                                <div className="balls2" id="balls2">
                                    <label></label>
                                    <label></label>
                                    <label></label>
                                    <label></label>
                                    <label></label>
                                    <label></label>
                                </div>
                            </div>
                            <div className="base"></div>
                        </div>
                    </div>
                </div>
            </div>
            <div id='resultado' className='resultado'>
                <ul>
                    <li id='ball1'></li>
                    <li id='ball2'></li>
                    <li id='ball3'></li>
                    <li id='ball4'></li>
                    <li id='ball5'></li>
                    <li id='powerball'></li>
                </ul>
                <label className='powerplay' id='pplay' style={{display: 'none'}}>Power Play 2X</label>
            </div>
            <div className='video'>
                <video id='video' src={Media} width='640px' height='360px'></video>
                {/* <ReactPlayer id='video' url='https://www.youtube.com/watch?v=ysz5S6PUM-U' controls={false} playing={true} /> */}
            </div>
        </section>
    )

    function sorteo() {
            document.getElementById('regresiva').style.display = 'block'
            document.getElementById('regresiva').style.display = 'none'
            document.getElementById('cero').style.display = 'block'
            document.querySelector('button').style.display = 'none'

            document.getElementById('video').playing = true
            document.getElementById('video').play()

            document.querySelector('div.balls').style.display = 'block'
            document.querySelector('div.balls2').style.display = 'block'

            document.querySelector('div.pozosfalse').style.display = 'block'
            document.querySelector('div.pozostrue').style.display = 'none'
            

            setTimeout(function () {
                document.getElementById('ball1').innerHTML = '60'
            }, 21000)
            setTimeout(function () {
                document.getElementById('ball2').innerHTML = '16'
            }, 25000)
            setTimeout(function () {
                document.getElementById('ball3').innerHTML = '21'
            }, 30000)
            setTimeout(function () {
                document.getElementById('ball4').innerHTML = '27'
            }, 35000)
            setTimeout(function () {
                document.getElementById('ball5').innerHTML = '61'
            }, 39000)
            setTimeout(function () {
                document.getElementById('powerball').innerHTML = '6'
                document.getElementById('pplay').style.display='block'
            }, 43000)
            setTimeout(function () {
                document.querySelector('div.balls').style.display = 'none'
                document.querySelector('div.balls2').style.display = 'none'
            }, 55000)
        }
    }
}

export default CuentaReg